/* 2. RESET & HEALING (Preparação do Terreno) */
/* ============================================
   _reset.scss — Hydra Reset (2026)
   Remove apenas o essencial.
   Não destrói estilos úteis.
   Trabalha em conjunto com normalize.scss.
============================================ */
/* 1. Remove margens padrão dos elementos mais problemáticos */
body, h1, h2, h3, h4, h5, h6, p, figure {
  margin: 0;
}

/* 2. Remove padding inesperado */
ul, ol {
  padding: 0;
}

/* 3. Remove bullets padrão (o normalize já trata de parte disto) */
ul, ol {
  list-style: none;
}

/* 4. Garantir que botões não têm estilos nativos inconsistentes */
button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

/* 5. Inputs e buttons sem estilos herdados estranhos */
input, button, textarea, select {
  font: inherit;
  color: inherit;
}

/* 6. Remove bordas e outlines agressivos (mas mantém acessibilidade) */
input, textarea, select {
  border: none;
  outline: none;
}

/* 7. Garantir que imagens não têm bordas antigas (IE legacy) */
img {
  border-style: none;
}

/* 8. Remove estilos nativos de anchors */
a {
  text-decoration: none;
}

/* 9. Remove comportamento estranho de <strong> e <em> em alguns browsers */
strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

/* 10. Remove espaçamento entre elementos inline-block */
:where(*) {
  letter-spacing: normal;
  word-spacing: normal;
}

/* 11. Tabelas sem bordas e espaçamentos inesperados */
/* fim do _reset.scss */
/* ============================================================
   heal.scss — Hydra Healing Layer (2026)
   ------------------------------------------------------------
   Este módulo atua como um "soro corretivo" para o layout.
   Corrige malfunctions comuns antes de o design entrar em cena.
   Modular, opcional e expansível.
============================================================ */
/* ------------------------------------------------------------
   1. Configurações (ativar/desativar tratamentos)
------------------------------------------------------------ */
/* ------------------------------------------------------------
   2. Box Model Healing
------------------------------------------------------------ */
*, *::before, *::after {
  box-sizing: border-box;
}

/* ------------------------------------------------------------
   3. Media Healing (imagens, vídeos, canvas, svg)
------------------------------------------------------------ */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
  height: auto;
  content-visibility: auto;
}

/* ------------------------------------------------------------
   4. Typography Healing
------------------------------------------------------------ */
html {
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

body {
  margin: 0;
}

/* ------------------------------------------------------------
   5. Forms Healing
------------------------------------------------------------ */
button, input, select, textarea {
  font: inherit;
  color: inherit;
}

textarea {
  resize: vertical;
}

/* ------------------------------------------------------------
   6. Lists Healing
------------------------------------------------------------ */
ul, ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

/* ------------------------------------------------------------
   7. Tables Healing
------------------------------------------------------------ */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* ------------------------------------------------------------
   8. Interaction Healing (anchors)
------------------------------------------------------------ */
a {
  text-decoration: none;
  color: inherit;
}

/* ============================================================
   Fim do Hydra Healing Layer
============================================================ */
/* 3. TEMAS (Configuração de Cores Globais) */
/* ============================================================
   THEMES — Bridge entre Sass Tokens e CSS Variables
============================================================ */
/* ============================================================
   COLORS — Design Tokens
============================================================ */
/* 1. Core colors */
/* 2. Brand colors */
/* 3. Semantic colors */
/* 4. Theme tokens */
/* 5. Component tokens */
/* ------------------------------------------------------------
   1. ROOT — valores base derivados dos tokens Sass
------------------------------------------------------------ */
:root {
  --bg: #ffffff;
  --text-primary: #000000;
  --text-heading: #f8fafc;
  --accent: #22d3ee;
  --accent-hover: #22d3ee;
  --accent-rgb: 0, 0, 0; /* substituído pelos temas */
  --text-on-accent: #ffffff;
  --link: #a9a9a9;
  --link-hover: #000000;
  --bg-img: none;
}

/* ------------------------------------------------------------
   2. LIGHT THEME
------------------------------------------------------------ */
html.light-theme {
  background-blend-mode: soft-light;
  opacity: 0.98;
  --bg: #ffffff;
  --text-primary: #1a1a1a;
  --text-heading: #000000;
  --accent-hover: #ffffff;
  --text-on-accent: #000000;
  --accent-rgb: 0, 85, 255;
  --link: #1a1a1a;
  --link-hover: #0055ff;
  --bg-img: url('../img/themes/light-bg.jpg');
}

/* ------------------------------------------------------------
   3. DARK THEME
------------------------------------------------------------ */
html.dark-theme {
  --bg: #101010;
  --text-primary: #b0b0b0;
  --text-heading: #ffffff;
  --accent: #ffffff;
  --accent-hover: #2980b9;
  --accent-rgb: 52, 152, 219;
  --text-on-accent: #000000;
  --link: #3498db;
  --link-hover: #ffffff;
  --bg-img: url('../img/themes/dark-bg.jpg');
}

/* ------------------------------------------------------------
   4. HYDRA THEME
------------------------------------------------------------ */
html.hydra-theme {
  --bg: #020202;
  --text-primary: #00cc00;
  --text-heading: #33ff33;
  --accent: #00ff41;
  --accent-hover: #008f11;
  --accent-rgb: 0, 255, 65;
  --text-on-accent: #000000;
  --link: #00ff41;
  --link-hover: #00cc33;
  --bg-img: url('../img/themes/hydra-bg.jpg');
}

/* ------------------------------------------------------------
   5. SOLARIZED THEME
------------------------------------------------------------ */
html.solarized-theme {
  --bg: #fdf6e3;
  --text-primary: #475b62;
  --text-heading: #002b36;
  --accent: #b58900;
  --accent-hover: #859900;
  --accent-rgb: 181, 137, 0;
  --text-on-accent: #ffffff;
  --link: #268bd2;
  --link-hover: #cb4b16;
  --bg-img: url('../img/themes/solarized-bg.jpg');
}

/* ------------------------------------------------------------
   6. DRACULA THEME
------------------------------------------------------------ */
html.dracula-theme {
  --bg: #282a36;
  --text-primary: #f8f8f2;
  --text-heading: #ffffff;
  --accent: #bd93f9;
  --accent-hover: #36013f;
  --accent-rgb: 189, 147, 249;
  --text-on-accent: #000000;
  --link: #f8f8f2;
  --link-hover: #bd93f9;
  --bg-img: url('../img/themes/dracula-bg.jpg');
}

/* ------------------------------------------------------------
   7. NEON THEME
------------------------------------------------------------ */
html.neon-theme {
  --bg: #0aff9d;
  --text-primary: #000000;
  --text-heading: #000000;
  --accent: #000000;
  --accent-hover: #222222;
  --accent-rgb: 0, 0, 0;
  --text-on-accent: #0aff9d;
  --link: #000000;
  --link-hover: #ffffff;
  --bg-img: url('../img/themes/neon-bg.jpg');
}

/* ------------------------------------------------------------
   8. BACKGROUND GLOBAL + FOOTER
------------------------------------------------------------ */
html {
  background-color: var(--bg);
  background-image: var(--bg-img);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  transition: background 0.5s ease-in-out;
}

footer {
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

/* 4. ESTRUTURA GLOBAL (Gera o CSS do Layout e Base) */
/* ============================================================
   LOGO SK — Círculo Dinâmico
   ------------------------------------------------------------
   - Círculo animado atrás do texto "SK"
   - Animação suave e contínua
   - Integração com temas (usa var(--accent))
============================================================ */
.logo-wrapper {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  cursor: pointer;
  /* Ajusta o tamanho total do logo */
  width: 60px;
  height: 60px;
}

/* CÍRCULO ANIMADO */
.logo-circle {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: var(--accent, #4fc3f7); /* fallback */
  z-index: -1;
  animation: logoPulse 3s ease-in-out infinite;
}

/* TEXTO SK */
.logo-text {
  font-size: 1.6rem;
  font-weight: 700;
  color: var(--text-primary, #fff);
  letter-spacing: 1px;
  user-select: none;
}

/* ANIMAÇÃO DO CÍRCULO */
@keyframes logoPulse {
  0% {
    transform: scale(1);
    opacity: 0.7;
  }
  50% {
    transform: scale(1.18);
    opacity: 0.4;
  }
  100% {
    transform: scale(1);
    opacity: 0.7;
  }
}
