/* ============================================================
   HERO SECTION — Corrigido e Responsivo
============================================================ */
.hero {
  padding-top: 2em;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 30vh;
}

.hero .container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.hero .container .hero_btn {
  align-self: center;
}

.hero_description {
  font-size: 1.2rem;
  color: var(--accent);
}

.hero_description--last {
  font-weight: bold;
  color: var(--link-hover);
}

/* HERO — RESPONSIVE */
@media (max-width: 1024px) {
  .hero {
    padding-top: 5rem;
    padding-bottom: 6rem;
  }
  .hero_description {
    font-size: 1.1rem;
  }
}
@media (max-width: 768px) {
  .hero {
    padding-top: 4rem;
    padding-bottom: 4rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
  .hero_description {
    font-size: 1rem;
  }
}
/* ============================================================
   HOME SECTION — Corrigido
============================================================ */
#home {
  position: relative;
  z-index: 1;
  display: none;
  opacity: 0;
  transform: translateY(40px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

#home.visible {
  display: block;
  min-height: 60vh;
  padding-top: 6rem;
  opacity: 1;
  transform: translateY(0);
}

.home-section .container {
  width: min(90%, 1200px);
  margin: 0 auto;
}

.home-heading {
  font-size: 1.6rem;
  margin-bottom: 2rem;
  color: var(--text-heading);
  text-align: center;
}

/* MAIN — usa flex, não grid */
.home-section .main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
}
@media (max-width: 1024px) {
  .home-section .main {
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
  }
}
@media (max-width: 768px) {
  .home-section .main {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
}

/* Parágrafos */
.home-section p {
  text-align: justify;
  text-justify: inter-word;
  hyphens: auto;
}
@media (max-width: 768px) {
  .home-section p {
    font-size: 0.9rem;
    text-align: center;
  }
}

