/* ============================================================
   Footer — Starter Kit (2026)
============================================================ */
footer {
  background: var(--bg-footer);
  color: var(--text-primary);
  flex-shrink: 0;
}

.site-footer {
  backdrop-filter: saturate(100%) blur(2px);
  -webkit-backdrop-filter: saturate(100%) blur(2px);
  width: 100%;
  padding: 1rem 1rem;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  max-width: 1240px;
  margin: auto;
  gap: 1.5rem;
  align-items: stretch;
}

/* ============================================================
   Estrutura de Coluna Unificada
============================================================ */
.footer-col {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.footer-col .footer-brand-name,
.footer-col .footer-heading {
  font-weight: 700;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  height: 1rem;
  display: flex;
  align-items: center;
}
.footer-col .footer-text-block,
.footer-col .footer-address,
.footer-col .footer-list {
  flex: 1;
}
.footer-col .footer-social-icons,
.footer-col .footer-credits {
  margin-top: auto;
  padding-top: 0.1rem;
}

/* ============================================================
   Ajustes Específicos
============================================================ */
.footer-text-block {
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
  max-width: 260px;
}

.footer-tagline-row {
  display: flex;
  align-items: center;
  gap: 0.1rem;
}

.footer-rocket {
  width: 24px;
  height: 24px;
  background: url("/img/icons/default/rocket-solid.svg") no-repeat center/contain;
  transition: transform 0.4s ease;
  margin-top: 0.2rem;
}
.footer-rocket:hover {
  transform: translate(4px, -4px) rotate(12deg);
  filter: drop-shadow(0 0 8px var(--accent));
}

.footer-social-icons {
  display: flex;
  gap: 1rem;
}
.footer-social-icons a i {
  font-size: 1.1rem;
  color: var(--text-primary);
