}
.footer-list .footer-link {
  color: var(--text-primary);
  opacity: 0.6;
  text-decoration: none;
  font-size: 0.9rem;
  display: block;
  margin-bottom: 0.6rem;
  transition: 0.3s;
}
.footer-list .footer-link:hover {
  opacity: 1;
  color: var(--accent);
  padding-left: 5px;
}

