/* ============================================================
   MAIN.SCSS — Organizado por Camadas
============================================================ */
/* 1. FERRAMENTAS & TOKENS (Lógica e Definições) */
/* ============================================================
   LAYOUT GLOBAL — Starter Kit
============================================================ */
/* Evita flicker de tema */
html {
  visibility: hidden;
}

html.light-theme,
html.dark-theme,
html.hydra-theme,
html.dracula-theme,
html.neon-theme,
html.solarized-theme {
  visibility: visible;
}

/* Evita blink da animação da hero */
html:not(.ready) .hero_description--last {
  opacity: 0 !important;
}

/* Agora sim, o layout */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* CORREÇÃO IMPORTANTE */
  background-color: transparent !important;
  background-image: none !important;
  color: var(--text-primary);
  margin: 0;
  padding: 0;
  font-family: var(--font-family, "Segoe Ui", "Tahoma", "Geneva", "Verdana", sans-serif);
}

footer {
  margin-top: auto;
  width: 100%;
}

/* ============================================================
   BLOB GLOBAL — Versão Intensa
============================================================ */
.layout {
  min-height: 12.5vh;
  position: relative;
  isolation: isolate;
  overflow: hidden;
  z-index: 0;
}

.layout::before,
.layout::after {
  content: "";
  position: fixed;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  z-index: -1;
  pointer-events: none;
  transform: translate(-50%, -50%);
}

.layout::before {
  width: 950px;
  height: 950px;
  background: radial-gradient(circle, rgba(var(--accent-rgb), 0.4) 0%, rgba(var(--accent-rgb), 0.1) 40%, transparent 75%);
  filter: blur(60px);
  animation: wave-outer 3s infinite ease-in-out;
}

.layout::after {
  width: 550px;
  height: 550px;
  background: radial-gradient(circle, rgba(var(--accent-rgb), 0.5) 0%, rgba(var(--accent-rgb), 0.2) 50%, transparent 80%);
  filter: blur(30px);
  animation: wave-inner 3s infinite ease-in-out;
}

@keyframes wave-outer {
  0%, 100% {
    transform: translate(-50%, -50%) scale(0.7);
    opacity: 0.2;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.6);
    opacity: 0.7;
  }
}
@keyframes wave-inner {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1.4);
    opacity: 0.3;
  }
  50% {
    transform: translate(-50%, -50%) scale(0.6);
    opacity: 0.8;
  }
}
/* ============================================================
   HEADINGS
============================================================ */
h1, h2, h3, h4, h5, h6 {
  color: var(--text-heading);
  margin: 0;
  padding: 0;
  font-weight: 700;
  line-height: 1.2;
}

/* LINKS */
a {
  color: var(--link);
  text-decoration: none;
  transition: color 0.2s ease;
}

a:hover {
  color: var(--link-hover);
}

/* MAIN WRAPPER */
.main {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: min(90%, 1200px);
  margin: 0 auto;
  gap: 2rem;
  padding: 2rem 0;
}

/* PARÁGRAFOS */
.main_paragraph1,
.main_paragraph2 {
  font-weight: 600;
  line-height: 1.6;
  transition: color 0.2s ease;
}

.main_paragraph1:hover {
  color: var(--accent);
  filter: brightness(1.25);
}

.main_paragraph2:hover {
  color: var(--link-hover);
  filter: brightness(1.15);
}

/* ============================
   RESPONSIVE — Via Mixins
   ============================ */
@media (max-width: 1024px) {
  .main {
    flex-direction: column;
    gap: 1.5rem;
  }
}
@media (max-width: 480px) {
  .main {
    width: 95%;
    gap: 1rem;
  }
  .main_paragraph1,
  .main_paragraph2 {
    font-size: 0.9rem;
    text-align: center;
  }
}
/* ============================================================
   Buttons Module — Starter Kit (2026)
============================================================ */
/* MENU BUTTON (hamburger) */
button.menu-btn {
  background: none;
  border: none;
  display: flex;
  align-items: center;
  cursor: pointer;
}
button.menu-btn .menu-btn_burguer {
  width: 24px;
  height: 2px;
  background: var(--link);
  position: relative;
  transition: 0.2s ease;
}
button.menu-btn .menu-btn_burguer::before, button.menu-btn .menu-btn_burguer::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background: var(--link);
  left: 0;
  transition: 0.2s ease;
}
button.menu-btn .menu-btn_burguer::before {
  top: -8px;
}
button.menu-btn .menu-btn_burguer::after {
  top: 8px;
}

/* ============================================================
   PRIMARY BUTTONS (hero, explore, generic)
============================================================ */
.button,
.hero_btn,
.hero_explore {
  display: inline-block;
  padding: 0.8rem 2.2rem;
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  /* CORREÇÃO IMPORTANTE */
  background-color: var(--accent);
  color: var(--text-on-accent); /* sem !important */
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  /* brilho suave */
  animation: button-glow 2s infinite ease-in-out;
}

.button:hover,
.hero_btn:hover,
.hero_explore:hover {
  background-color: var(--accent-hover);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(var(--accent-rgb), 0.6);
  animation-play-state: paused;
}

/* animação */
@keyframes button-glow {
  0%, 100% {
    box-shadow: 0 0 10px rgba(var(--accent-rgb), 0.2);
    filter: brightness(1);
  }
  50% {
    box-shadow: 0 0 25px rgba(var(--accent-rgb), 0.5);
    filter: brightness(1.15);
  }
}
/* estado escondido */
.hero_btn.hidden {
  opacity: 0;
  transform: translateY(10px);
  pointer-events: none;
}

/* ============================
   Tokens de design: Ícones
    (cores, espaçamentos, tipografia, sombras)
============================ */
.icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  transition: filter 0.2s ease;
}

/* Classe utilitária para componentes escuros */
.dark-ui .icon {
  filter: invert(1);
}

/* Tema claro → ícones pretos */
.light-theme .icon {
  filter: invert(0);
}

/* Temas escuros → ícones brancos */
.dark-theme .icon,
.neon-theme .icon,
.dracula-theme .icon,
.solarized-theme .icon,
.hydra-theme .icon {
  filter: invert(1);
}

/* 5. COMPONENTES (O que o PUG injeta) */
/* 6. ANIMAÇÕES & RESPONSIVE (Ajustes Finais) */
/* ============================================================
   Hero Description Animations
   ------------------------------------------------------------
   Animação suave para a última linha da hero
============================================================ */
/* Estado inicial */
.hero_description--last {
  opacity: 0;
  transform: translateY(10px);
}

/* Estado animado */
.hero_description--last.animate {
  animation: heroLastFade 0.8s ease forwards 0.3s;
}

@keyframes heroLastFade {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
